@font-face {
  font-family: "Llewie";
  src: url(./Assets/Fonts/Llewie.ttf);
}

body {
  width: 100%;
  height: 100vh;
  font-family: "Poppins";
  background-color: #050810;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: #ff7200;
  border: 2px solid #fdfdfd;
  border-radius: 20px;
}
::-webkit-scrollbar-track {
  background: #e8fdee;
}
::selection {
  background-color: #ff7200;
  color: #f5f5f5;
}

.loadingAnimationStyles {
  position: fixed;
  z-index: 10000000;
  width: 100%;
  height: 100vh;
  background-color: #050810;
  top: 0%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.spaceLoader {
  width: 300px;
  height: 300px;
}
